import React, { Component } from 'react';

class FeedCard extends Component {

  toggleExpansion = () => {
    this.props.toggleExpansion('right');
    this.props.setActive(this.props.feedId);
  }

  render() {
    return (
      <div className="feed-card">
        <div className="feed-card-header">
          Card
          <br/>
          Feed: {this.props.feedId || 'waterfall expansion'}
          <br/>
          <button onClick={ this.toggleExpansion }>Click to open side</button>
        </div>
      </div>
    );
  }
}

export default FeedCard;



