import React, { Component } from 'react';
import _ from 'lodash';
import { CSSTransition, TransitionGroup } from 'react-transition-group'

import {Dock} from './dock'
import Horizontal from './horizontal';
import Vertical from './vertical';
import Side from './side';
import Header from './header';
import Feed from './feed';
import './App.css';


const Fade = ({ children, ...props }) => (
  <CSSTransition
    {...props}
    timeout={500}
    classNames="fade"
  >
    {children}
  </CSSTransition>
);


class App extends Component {
  constructor() {
    super();
    window.po = this;
    this.state = {
      rightDockExpanded: false,
      activeFeed: null,
      topExpanded: false,
      leftExpanded: false,
      rightExpanded: false,
      bottomExpanded: false,
      leftWidth: 760,
      rightWidth: 760,
      topHeight: 700,
      bottomHeight: 700,
      feeds: [
        {
          id: 1,
          name: 'Feed: one',
        },
        {
          id: 2,
          name: 'Feed: two',
        },
        {
          id: 3,
          name: 'Feed: three',
        },
      ],
      // HACK
      allFeeds: [
        {
          id: 1,
          name: 'Feed: one',
        },
        {
          id: 2,
          name: 'Feed: two',
        },
        {
          id: 3,
          name: 'Feed: three',
        },
      ],
    };
  };

  componentWillUpdate(nextProps, nextState) {
    if (!_.isEqual(this.state.rightExpanded, nextState.rightExpanded) && nextState.rightExpanded === false) {
      this.setState({rightDockExpanded: false});
      console.log('here')
    }

    if (!_.isEqual(this.state.leftExpanded, nextState.leftExpanded) && nextState.leftExpanded === false) {
      this.setState({leftDockExpanded: false});
    }
  }

  setActive = (feedId, setActive) => {
    this.setState({
      activeFeed: setActive ? feedId : false,
      feeds: feedId ? _.filter(this.state.allFeeds, (feed) => {
        return feed.id === feedId;
      }) : this.state.allFeeds,
    });
  };

  toggleSides = (left, right) => {
    this.setState({leftExpanded: left, rightExpanded: right});
    if (!(left || right)) {
      this.setActive();
    }
  }

  get feeds() {
    return _.map(this.state.feeds, (feed) => {
      return(
        <Feed
          setActive={this.setActive}
          key={feed.id}
          feedId={feed.id}
          name={feed.name}
        />
      )
    });
  };

  get topContent() {
    return (
      <div className="content" style={{height: this.state.topHeight}}>
        <h1>hello</h1>
      </div>
    )
  }

  get leftContent() {
    return (
      <div className="content">
        <button onClick={ () => this.toggleSides(false, true) }>
          open rights
        </button>
        <br/>
        <button onClick={ () => this.toggleSides(false, false) }>
          Dashboard
        </button>
      </div>
    )
  };

  get rightContent() {
    return (
      <div style={{paddingLeft: 20}}>
        <Dock
          expanded={ this.state.rightDockExpanded }
        />
        <button onClick={ () => this.toggleSides(true, false) }>
          open left
        </button>
        <br/>
        <button onClick={ () => this.toggleSides(this.state.leftExpanded, false) }>
          close right
        </button>
        <br/>
        <button onClick={ () => this.toggleSides(false, false) }>
          Dashboard
        </button>
        <br/>
        <button onClick={ () => this.setState({rightDockExpanded: !this.state.rightDockExpanded}) }>
          Toggle Dock
        </button>
      </div>
    )
  };

  toggleExpansion = (position) => {
    const toggle = `${position}Expanded`;
    this.setState({[toggle]: !this.state[toggle]});
  };

  render() {
    return (
      <div className="container">
        
        <div style={{height: 80}}>
          <Header/>
          <Horizontal
            expanded={ this.state.topExpanded }
            position="top"
            toggleExpansion={ this.toggleExpansion }
            barColor="red"
          />
        </div>

        <div className="main-content">
          <Vertical
            expanded={ this.state.leftExpanded }
            position="left"
            toggleExpansion={ this.toggleExpansion }
            barColor="#003242"
          />
          <div className="center">
            <TransitionGroup component="feed-transition-wrapper">
              {this.state.feeds.map((feed, i) => (
                <Fade key={feed.id}>
                  <Feed
                    isShown="true"
                    setActive={this.setActive}
                    key={feed.id}
                    feedId={feed.id}
                    toggleExpansion={ this.toggleExpansion }
                    barColor="red"
                    name={feed.name}
                  />
                </Fade>
              ))}
            </TransitionGroup>
          </div>
          <Vertical
            expanded={ this.state.rightExpanded }
            position="right"
            toggleExpansion={ this.toggleExpansion }
            barColor="#f3032e"
            content={ this.rightContent }
          />
        </div>

        <Horizontal
          expanded={ this.state.bottomExpanded }
          position="bottom"
          toggleExpansion={ this.toggleExpansion }
        />

      </div>
    );
  }
}

export default App;



