import React, { Component } from 'react';

class Vertical extends Component {
  
  render() {
    return (
      <div 
      	className="vertical-bar" 
      	style={{width: this.props.expanded ? 'calc(100vw - 355px)' : 20}}
      >
      	{ this.props.content }
      </div>
    );
  }
}

export default Vertical;



