import React, { Component } from 'react';

class Side extends Component {

  get expandedStyle() {
    const collapsed = -Math.abs(this.props.contentWidth - 20);
    return {
      [this.props.position]: this.props.showExpanded ? 0 : collapsed,
      width: this.props.contentWidth,
    }
  };

  get sideMenu() {
    return (
      <div
        className="side-menu"
        onClick={ () => this.props.toggleExpand(this.props.position) }
        style={{
          backgroundColor: this.props.backgroundColor,
          float: this.props.position === 'right' ? 'left' : 'right',
        }}
      >
        <div
          className="dots"
          style={{[this.props.position === 'right' ? 'left' : 'right']: 8}}
        >
          <i className="fa fa-ellipsis-v"></i>
        </div>
      </div>
    );
  };

  render() {
    return (
      <div
        className="side-wrapper"
        style={{right: this.props.position === 'right' ? this.props.contentWidth : 'initial'}}
      >
        <div
          className="expanded"
          style={this.expandedStyle}
        >
          { this.props.content }
          { this.sideMenu }
        </div>
      </div>
    );
  }
}

export default Side;



