import React from 'react';
import _ from 'lodash';
import {DockItem} from './dock-item'

export class Dock extends React.Component {
	constructor() {
		super();
		console.log(this.props)
		this.state = {
			dockItems: [
				{
					id: 1,
					icon: 'calendar',
					name: 'Date',
				},
				{
					id: 2,
					icon: 'user',
					name: 'Person',
				},
				{
					id: 3,
					icon: 'users',
					name: 'People',
				},
				{
					id: 4,
					icon: 'globe',
					name: 'Earth',
				},
			],
		}
	}

	showItem(e){
		e.preventDefault()
	}

	get expPosition(){
		const right = this.props.expanded ? 0 : -35;

		return {
			right
		}
	}

  get dockItems() {
    return _.map(this.state.dockItems, (item) => {
      return(
        <DockItem
          key={item.id}
          id={item.id}
          name={item.name}
          icon={item.icon}
        />
      )
    });
  };

  render() {
    return (
      <div className="dock" style={ this.expPosition }>
				<div onClick={this.showItem}>{ this.dockItems }</div>
      </div>
    )
  }
}


